#wrap {
  padding: 30px;
}
#wrap svg {
  width: 100%;
  height: 210px;
}
#wrap circle.circles {
  stroke: #999;
  stroke-width: 1px;
  fill: none;
}
#wrap circle.circlepoint {
  stroke: #08c;
  stroke-width: 1px;
  fill: none;
}
#wrap line.lines,
#wrap line.lines2 {
  stroke: #08c;
  stroke-width: 3px;
}
#wrap line.lines2 {
  stroke-width: 1px;
}
#wrap path.paths {
  stroke: #08c;
  stroke-width: 1px;
  fill: none;
}
