body {
  padding: 5px;
  background: #f3f4f5 !important;
}
body div {
  font-size: 16px;
}
body .list .list-li {
  float: left;
  padding: 5px 0;
  width: 160px;
  height: 132px;
}
body .list .list-li .list-li-box {
  padding: 8px;
}
body .list .list-li .list-li-box-no-img {
  height: 100%;
}
body .list .list-li .list-li-box-no-img > div {
  border-radius: 5px;
  height: 100px;
  padding: 10px;
  overflow: hidden;
  line-height: 100%;
  background: #f3f4f5;
}
body .list .list-li img {
  width: 150px;
  height: 100px;
  border: 1px solid #eee;
  border-radius: 5px;
}
body .list .list-li img:hover {
  border-color: #08c;
}
body .list a {
  text-decoration: none;
  color: #555;
}
body .list a:hover {
  text-decoration: underline;
  color: #08c;
}

#drawings {
  background: none;
}

.tree-wrap {
  padding: 12px;
  background: #fff;
  border-radius: 10px;
}
.tree-wrap .ant-btn {
  border-radius: 5px;
}
.tree-wrap .selectGroupWrap {
  overflow-y: auto;
  border-radius: 5px;
  padding: 0 5px 0 0px;
  position: relative;
}
.tree-wrap .selectGroupWrap .selectGroupLi {
  padding: 5px 5px 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 5px 0 0 10px;
  position: relative;
  /* 开关组件 */
}
.tree-wrap .selectGroupWrap .selectGroupLi:first-child {
  margin-left: 0px;
  border: none;
  padding: 0px 5px 5px 0;
  overflow: hidden;
}
.tree-wrap .selectGroupWrap .selectGroupLi:before,
.tree-wrap .selectGroupWrap .selectGroupLi:after {
  content: "";
  position: absolute;
  top: -42px;
  border-width: 0 0 2px 2px;
  left: -11px;
  width: 10px;
  height: calc(50% + 58px);
  border-color: #08c;
  border-style: solid;
}
.tree-wrap .selectGroupWrap .selectGroupLi:after {
  top: 50%;
  border-width: 0 0 0 2px;
}
.tree-wrap .selectGroupWrap .selectGroupLi:last-child:after {
  top: -42px;
}
.tree-wrap .selectGroupWrap .selectGroupLi .selectListLi {
  padding: 6px 5px 6px 10px;
  border-radius: 5px;
  position: relative;
}
.tree-wrap .selectGroupWrap .selectGroupLi .selectListLi::before {
  content: "";
  position: absolute;
  top: -25px;
  border-width: 0 0 2px 2px;
  left: 0px;
  width: 10px;
  height: calc(50% + 27px);
  border-color: #08c;
  border-style: solid;
}
.tree-wrap .selectGroupWrap .selectGroupLi .selectListLi > div {
  display: inline;
}
.tree-wrap .selectGroupWrap .selectGroupLi .selectListLi button {
  vertical-align: top;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch {
  background: #08c;
  min-width: 121px !important;
  border-radius: 5px;
  height: 32px;
  line-height: 32px;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch .ant-switch-checked:after {
  margin-left: 0px;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch .ant-switch-inner {
  margin-left: 82px;
  font-size: 14px !important;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch .ant-switch-handle {
  height: 30px;
  width: 60px;
  top: 1px;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch .ant-switch-handle::before {
  border-radius: 5px 0 0 5px;
  content: "AND";
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch.ant-switch-checked .ant-switch-inner {
  margin-left: 0;
  width: 60px;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch.ant-switch-checked .ant-switch-handle {
  left: 60px;
}
.tree-wrap .selectGroupWrap .selectGroupLi .ant-switch.ant-switch-checked .ant-switch-handle::before {
  content: "OR";
  border-radius: 0 5px 5px 0;
}
.tree-wrap .hide {
  display: none!important;
}

#wrap {
  padding: 30px;
}
#wrap svg {
  width: 100%;
  height: 210px;
}
#wrap circle.circles {
  stroke: #999;
  stroke-width: 1px;
  fill: none;
}
#wrap circle.circlepoint {
  stroke: #08c;
  stroke-width: 1px;
  fill: none;
}
#wrap line.lines,
#wrap line.lines2 {
  stroke: #08c;
  stroke-width: 3px;
}
#wrap line.lines2 {
  stroke-width: 1px;
}
#wrap path.paths {
  stroke: #08c;
  stroke-width: 1px;
  fill: none;
}

#svg-wrap-force {
  background: #08c;
}
#svg-wrap-force svg .link {
  stroke: #fff;
  stroke-width: 1.5px;
}
#svg-wrap-force svg .node {
  cursor: move;
  fill: #08c;
  stroke: #fff;
  stroke-width: 1.5px;
}
#svg-wrap-force svg .node.fixed {
  fill: #fff;
}

