body {
  padding: 5px;
  background: #f3f4f5 !important;
}
body div {
  font-size: 16px;
}
body .list .list-li {
  float: left;
  padding: 5px 0;
  width: 160px;
  height: 132px;
}
body .list .list-li .list-li-box {
  padding: 8px;
}
body .list .list-li .list-li-box-no-img {
  height: 100%;
}
body .list .list-li .list-li-box-no-img > div {
  border-radius: 5px;
  height: 100px;
  padding: 10px;
  overflow: hidden;
  line-height: 100%;
  background: #f3f4f5;
}
body .list .list-li img {
  width: 150px;
  height: 100px;
  border: 1px solid #eee;
  border-radius: 5px;
}
body .list .list-li img:hover {
  border-color: #08c;
}
body .list a {
  text-decoration: none;
  color: #555;
}
body .list a:hover {
  text-decoration: underline;
  color: #08c;
}
